'use client';
import Footer from '@/components/footer/page';
import Header from '@/components/header/page';
import Sidebar from '@/components/sidebar/page';
import React, { useState } from 'react';

export default function InventoryManagement() {
  const [inventoryItem, setInventoryItem] = useState({
    name: '',
    description: '',
    category: '',
    price: '',
    quantity: 0,
    status: 'In Stock',
    sku: '',
  });

  const [inventoryList, setInventoryList] = useState([
    // Example initial data for demonstration
    {
      id: 1,
      name: 'Product 1',
      description: 'Description of product 1',
      category: 'Electronics',
      price: 100,
      quantity: 50,
      status: 'In Stock',
      sku: 'P1SKU',
    },
    {
      id: 2,
      name: 'Product 2',
      description: 'Description of product 2',
      category: 'Clothing',
      price: 30,
      quantity: 100,
      status: 'In Stock',
      sku: 'P2SKU',
    },
  ]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setInventoryItem((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Simulate adding a new item (in a real app, you would send this to an API or database)
    setInventoryList((prev) => [
      ...prev,
      { ...inventoryItem, id: prev.length + 1, quantity: parseInt(inventoryItem.quantity) },
    ]);
    setInventoryItem({
      name: '',
      description: '',
      category: '',
      price: '',
      quantity: 0,
      status: 'In Stock',
      sku: '',
    });
  };

  const handleUpdateQuantity = (id: number, newQuantity: number) => {
    setInventoryList((prev) =>
      prev.map((item) =>
        item.id === id ? { ...item, quantity: newQuantity } : item
      )
    );
  };

  return (
    <div className="flex flex-col min-h-screen bg-gray-100">
      <div className="flex flex-grow">
        <Sidebar />
        <div className="flex flex-col flex-grow">
          <Header />
          <main className="p-8 bg-white rounded-lg shadow-md mt-6 mx-4 md:mx-8">
            <h2 className="text-3xl font-semibold text-gray-800 mb-8 text-center">Inventory Management</h2>

            {/* Add New Item Form */}
            <section className="mb-8">
              <h3 className="text-2xl font-semibold text-gray-700 mb-4">Add New Item</h3>
              <form onSubmit={handleSubmit} className="space-y-6">
                <div className="grid grid-cols-1 gap-6 md:grid-cols-2">
                  <div>
                    <label className="text-lg font-medium text-gray-700">Item Name</label>
                    <input
                      type="text"
                      name="name"
                      value={inventoryItem.name}
                      onChange={handleChange}
                      className="mt-2 w-full px-4 py-3 rounded-md border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                      placeholder="Enter item name"
                    />
                  </div>
                  <div>
                    <label className="text-lg font-medium text-gray-700">Description</label>
                    <textarea
                      name="description"
                      value={inventoryItem.description}
                      onChange={handleChange}
                      className="mt-2 w-full px-4 py-3 rounded-md border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                      placeholder="Enter item description"
                    />
                  </div>
                  <div>
                    <label className="text-lg font-medium text-gray-700">Category</label>
                    <select
                      name="category"
                      value={inventoryItem.category}
                      onChange={handleChange}
                      className="mt-2 w-full px-4 py-3 rounded-md border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                    >
                      <option value="">Select Category</option>
                      <option value="Electronics">Electronics</option>
                      <option value="Clothing">Clothing</option>
                      <option value="Furniture">Furniture</option>
                    </select>
                  </div>
                  <div>
                    <label className="text-lg font-medium text-gray-700">Price</label>
                    <input
                      type="number"
                      name="price"
                      value={inventoryItem.price}
                      onChange={handleChange}
                      className="mt-2 w-full px-4 py-3 rounded-md border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                      placeholder="Enter item price"
                    />
                  </div>
                  <div>
                    <label className="text-lg font-medium text-gray-700">Quantity</label>
                    <input
                      type="number"
                      name="quantity"
                      value={inventoryItem.quantity}
                      onChange={handleChange}
                      className="mt-2 w-full px-4 py-3 rounded-md border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                      placeholder="Enter item quantity"
                    />
                  </div>
                  <div>
                    <label className="text-lg font-medium text-gray-700">SKU</label>
                    <input
                      type="text"
                      name="sku"
                      value={inventoryItem.sku}
                      onChange={handleChange}
                      className="mt-2 w-full px-4 py-3 rounded-md border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                      placeholder="Enter item SKU"
                    />
                  </div>
                  <div>
                    <label className="text-lg font-medium text-gray-700">Status</label>
                    <select
                      name="status"
                      value={inventoryItem.status}
                      onChange={handleChange}
                      className="mt-2 w-full px-4 py-3 rounded-md border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                    >
                      <option value="In Stock">In Stock</option>
                      <option value="Out of Stock">Out of Stock</option>
                    </select>
                  </div>
                </div>

                <div className="mt-8 flex justify-center">
                  <button
                    type="submit"
                    className="px-6 py-3 bg-indigo-600 text-white font-semibold rounded-md hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                  >
                    Add Item
                  </button>
                </div>
              </form>
            </section>

            {/* Inventory List */}
            <section>
              <h3 className="text-2xl font-semibold text-gray-700 mb-4">Inventory List</h3>
              <table className="min-w-full table-auto">
                <thead>
                  <tr>
                    <th className="px-6 py-3 text-left text-gray-700">Item</th>
                    <th className="px-6 py-3 text-left text-gray-700">Category</th>
                    <th className="px-6 py-3 text-left text-gray-700">Price</th>
                    <th className="px-6 py-3 text-left text-gray-700">Quantity</th>
                    <th className="px-6 py-3 text-left text-gray-700">Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {inventoryList.map((item) => (
                    <tr key={item.id}>
                      <td className="px-6 py-4 text-gray-700">{item.name}</td>
                      <td className="px-6 py-4 text-gray-700">{item.category}</td>
                      <td className="px-6 py-4 text-gray-700">${item.price}</td>
                      <td className="px-6 py-4 text-gray-700">
                        <input
                          type="number"
                          value={item.quantity}
                          onChange={(e) => handleUpdateQuantity(item.id, parseInt(e.target.value))}
                          className="w-20 px-2 py-1 border border-gray-300 rounded-md"
                        />
                      </td>
                      <td className="px-6 py-4 text-gray-700">
                        <button className="px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700">Edit</button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </section>
          </main>
        </div>
      </div>
      <Footer />
    </div>
  );
}
